export function PageTheme(Theme)
{
  switch(Theme)
  {
    case "Light":
    {
      document.querySelectorAll(".dark-background-full").forEach((e) =>
      {
        e.classList.replace("dark-background-full", "light-background-full");
      });
      document.querySelectorAll(".dark-background").forEach((e) =>
      {
        e.classList.replace("dark-background", "light-background");
      });
      document.querySelectorAll(".light-text").forEach((e) =>
      {
        e.classList.replace("light-text", "dark-text");
      });
      document.querySelectorAll(".light-border").forEach((e) =>
      {
        e.classList.replace("light-border", "dark-border");
      });
      document.querySelectorAll(".invert").forEach((e) =>
      {
        e.classList.replace("invert", "no-invert");
      });
      document.querySelectorAll(".dark-backhover").forEach((e) =>
      {
        e.classList.replace("dark-backhover", "light-backhover");
      });
      document.querySelectorAll(".svg-dark-back").forEach((e) =>
      {
        e.classList.replace("svg-dark-back", "svg-light-back");
      });
      document.querySelectorAll(".svg-dgray-back").forEach((e) =>
      {
        e.classList.replace("svg-dgray-back", "svg-lgray-back");
      });
      document.querySelectorAll(".dark-boxshadow").forEach((e) =>
      {
        e.classList.replace("dark-boxshadow", "light-boxshadow");
      });

      break;
    }
    case "Dark":
    {
      document.querySelectorAll(".light-background-full").forEach((e) =>
      {
        e.classList.replace("light-background-full", "dark-background-full");
      });
      document.querySelectorAll(".light-background").forEach((e) =>
      {
        e.classList.replace("light-background", "dark-background");
      });
      document.querySelectorAll(".dark-text").forEach((e) =>
      {
        e.classList.replace("dark-text", "light-text");
      });
      document.querySelectorAll(".dark-border").forEach((e) =>
      {
        e.classList.replace("dark-border", "light-border");
      });
      document.querySelectorAll(".no-invert").forEach((e) =>
      {
        e.classList.replace("no-invert", "invert");
      });
      document.querySelectorAll(".light-backhover").forEach((e) =>
      {
        e.classList.replace("light-backhover", "dark-backhover");
      });
      document.querySelectorAll(".svg-light-back").forEach((e) =>
      {
        e.classList.replace("svg-light-back", "svg-dark-back");
      });
      document.querySelectorAll(".svg-lgray-back").forEach((e) =>
      {
        e.classList.replace("svg-lgray-back", "svg-dgray-back");
      });
      document.querySelectorAll(".light-boxshadow").forEach((e) =>
      {
        e.classList.replace("light-boxshadow", "dark-boxshadow");
      });

      break;
    }
    default:
    {
      document.querySelectorAll(".dark-background-full").forEach((e) =>
      {
        e.classList.replace("dark-background-full", "light-background-full");
      });
      document.querySelectorAll(".dark-background").forEach((e) =>
      {
        e.classList.replace("dark-background", "light-background");
      });
      document.querySelectorAll(".light-text").forEach((e) =>
      {
        e.classList.replace("light-text", "dark-text");
      });
      document.querySelectorAll(".light-border").forEach((e) =>
      {
        e.classList.replace("light-border", "dark-border");
      });
      document.querySelectorAll(".invert").forEach((e) =>
      {
        e.classList.replace("invert", "no-invert");
      });
      document.querySelectorAll(".dark-backhover").forEach((e) =>
      {
        e.classList.replace("dark-backhover", "light-backhover");
      });
      document.querySelectorAll(".svg-dark-back").forEach((e) =>
      {
        e.classList.replace("svg-dark-back", "svg-light-back");
      });
      document.querySelectorAll(".svg-dgray-back").forEach((e) =>
      {
        e.classList.replace("svg-dgray-back", "svg-lgray-back");
      });
      document.querySelectorAll(".dark-boxshadow").forEach((e) =>
      {
        e.classList.replace("dark-boxshadow", "light-boxshadow");
      });

      break;
    }
  }
  if(localStorage.getItem("Theme"))
  {
      document.querySelectorAll(".theme-button").forEach((b) => 
      {
          localStorage.getItem("Theme") === "Light"?
          b.children[0].style.transform = "rotate(0)"
          :
          b.children[0].style.transform = "rotate(180deg)"
      })
  }
}

export function Adjustment()
{
  window.addEventListener("scroll", function()
  {
    ComponentClose();
    if(this.document.scrollingElement.scrollTop >= this.document.documentElement.clientHeight)
    {
      this.document.querySelector(".backtop-btn").style.display = "initial";
    }
    else
    {
      this.document.querySelector(".backtop-btn").style.display = "none";
    }
  });
  window.addEventListener("resize", function()
  {
    Simplify();
    ComponentClose();
    if(!this.document.getElementById("resp-nav").style.display.includes("none"))
    {
    this.document.getElementById("resp-nav").style.display = "none";
    
    const left = this.document.getElementById("resp-nav-button").children[0];
    const mid = this.document.getElementById("resp-nav-button").children[1];
    const right = this.document.getElementById("resp-nav-button").children[2];

    if(mid.style.display.includes("none"))
    {
    left.animate([{transform: "rotate(45deg)"}, {transform: "rotate(0)"}], {duration: 200})
    .addEventListener("finish", () =>{left.style.position = "static"; left.style.transform = "rotate(0)";});
    right.animate([{transform: "rotate(-45deg)"}, {transform: "rotate(0)"}], {duration: 200})
    .addEventListener("finish", () =>{right.style.position = "static"; right.style.transform = "rotate(0)"; mid.style.display = "block";});
    this.document.getElementById("resp-nav").animate(
        [{top: "0", opacity: 1}, {top: "-150px", opacity: 0}], 
        {duration: 300}).addEventListener("finish", () => {this.document.getElementById("resp-nav").style.display = "none";});
    }
    else
    {
        mid.style.display = "block";
        left.style.transform = "rotate(0)";
        right.style.transform = "rotate(0)";
        left.style.position = "static";
        right.style.position = "static";
        this.document.getElementById("resp-nav").style.display = "none";
    }
  }
  });
}

export function Simplify()
{
  if(window.location.pathname.includes("entrar") || window.location.pathname.includes("registrar")
  || document.documentElement.clientWidth <= 900)
  {
    document.getElementById("header-svg").style.display = "none";
    document.getElementById("footer").style.minHeight = "10vh";
    document.querySelector(".footer-links-container").style.display = "none";
  }
  else
  {
    document.getElementById("header-svg").style.display = "block";
    document.getElementById("footer").style.minHeight = "19vh";
    document.querySelector(".footer-links-container").style.display = "block";
  }
}

export function ComponentClose()
{
    document.querySelectorAll(".closable").forEach((c) => c.style.display = "none");
}