import './App.css';
import './Themes.css'
import Content from './Content';
import Footer from './Footer';
import Header from './Header';
import { useEffect } from 'react';
import { Adjustment, PageTheme, Simplify } from './StyleScripts';
import BackTopButton from './BackTopButton';
import { FetchData, LoadBooks } from "./load";
import Breadcrumb from './Breadcrumb';
import { useLocation } from 'react-router';

function App(props) 
{
  document.title = "Biblioteca X";
  document.scrollingElement.scroll({top: 0});

  let path = useLocation();

  useEffect(() =>
  {
    FetchData();
    PageTheme(localStorage.getItem("Theme") || "Light");
    Adjustment();
    Simplify();
    if(!sessionStorage.getItem("Books"))
    {
      LoadBooks();
    }
  });

  return (
    <div className="App light-background">
      <Header />
      <Breadcrumb path={path.pathname}/>
      <Content />
      <BackTopButton />
      <Footer />
    </div>
  );
}

export default App;