import { useEffect, useState } from "react";
import "./Book.css";
import { PageTheme, Simplify } from "./StyleScripts";

export default function Book(props)
{
    const [book, setBook] = useState({});
    const [errorMsg, setErrorMsg] = useState(null);

    document.title = `Biblioteca X | Livro - ${book.name}`;
    document.scrollingElement.scroll({top: 0});

    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
        Load(book, setBook, setErrorMsg);
        Avaliability(book);
        Simplify();
    }, [book]);

    return(
        <section className="content book-container light-background">
            <div className="book-mid light-background-full light-text">
                <img className="book-image" alt="Foto do livro." src={book.image || "/examples/one/genericBook.png"}/>
                <h3 className="book-title">{book.name || errorMsg}</h3>
                {
                    book && !errorMsg?
                    <div className="book-info light-text">
                        <div className="basic-info light-text">
                            <p className="info"><span className="label">Autor: </span>{book.author || "---"}</p>
                            <p className="info"><span className="label">Pubicado em: </span>{book.pubdate || "---"}</p>
                            <p className="info"><span className="label">Número de páginas: </span>{book.pages || "---"}</p>
                            {
                                book.group? !book.group.toLowerCase().includes("none")?
                                <p className="info"><span className="label">Cole&ccedil;&atilde;o: </span>{book.group}</p>
                                :
                                null
                                :
                                null
                            }
                        </div>
                        <div className="book-status light-text">
                            <div className="status">
                                <img className="book-aval-i no-invert" id="book-aval-i"
                                alt="Imagem do status dos livro." src={null}/>
                                <p id="book-aval" className="book-aval"></p>
                            </div>
                            <div className="status">
                            <img className="book-quantity-i no-invert" id="book-quantity-i"
                                alt="Imagem quantidade de livros." src={"/examples/one/bookPileIcon.png"}/>
                                <p className="book-quantity" id="book-quantity"></p>
                            </div>
                        </div>
                        <div className="book-resume light-text">
                            <h4 className="subtitle">Sinopse do livro</h4>
                            <p className="text">{book.synopsis || "Sem sinopse disponível."}</p>
                        </div>
                    </div>
                    : null
                }
            </div>
        </section>
    )
}
function Load(book, setBook, setErrorMsg)
{
    if(!book._id)
    {
        const matched = JSON.parse(sessionStorage.getItem("Books")).filter((bs) =>
        {
            return bs._id === window.location.search.replace("?id=", "");
        }).pop();
        
        if(matched)
        {
            setBook(matched);
        }
        else
        {
            setErrorMsg("Nenhum livro encontrado com esse ID.");
        }
    }
}
function Avaliability(book)
{
    if(book.status)
    {
        if(book.status.avaliable && book.status.avalQuantity > 0)
        {
            document.getElementById("book-aval").style.color = "green";
            document.getElementById("book-aval").innerText = "O livro está disponível.";
            document.getElementById("book-aval-i").src = "/examples/one/checkedIcon.png";
            if(book.status.quantity === 1)
            {
                document.getElementById("book-quantity").innerText = `Temos um exemplar disponível.`;
            }
            else if(book.status.quantity > 1)
            {
                document.getElementById("book-quantity").innerText = `Temos ${book.status.quantity} exemplares e ${book.status.quantity===book.status.avalQuantity? "ambos":book.status.avalQuantity} ${book.status.avalQuantity > 1? "estão disponíveis.":"está disponível."}`;
            }
            else if(book.status.avalQuantity === 1)
            {
                document.getElementById("book-quantity").innerText = `Temos ${book.status.quantity} exemplares e 
                um está disponível.`;
            }
        }
        else
        {
            document.getElementById("book-aval").style.color = "crimson";
            document.getElementById("book-aval").innerText = "O livro está indisponível.";
            document.getElementById("book-aval-i").src = "/examples/one/xIcon.png";
            if(book.status.quantity > 1)
            {
                document.getElementById("book-quantity").innerText = `Temos ${book.status.quantity} exemplares e 
                nenhum está disponível.`;
            }
            else
            {
                document.getElementById("book-quantity").innerText = `Temos ${book.status.quantity} exemplar e 
                não está disponível.`;
            }
        }
    }
}