import axios from "axios";
import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { ComponentClose, PageTheme } from "./StyleScripts";

export default function Header(props)
{
    useEffect(() =>
    {
        document.querySelectorAll(".link").forEach((l) =>
        {
            l.addEventListener("click", () => {RespNavClose(true);});
        });
        if(localStorage.getItem("Theme"))
        {
            document.querySelectorAll(".theme-button").forEach((b) => 
            {
                localStorage.getItem("Theme") === "Light"?
                b.children[0].style.transform = "rotate(0)"
                :
                b.children[0].style.transform = "rotate(180deg)"
            })
        }
    });

    return(
        <header className="header" id="header">
            <div className="top-header">
                <NavLink className="link logo-link" to="/examples/one"><img alt="Logo da biblioteca." src="/examples/one/bookLogo.png"/>Biblioteca X</NavLink>
                <nav className="header-nav">
                    <ul className="header-nav-list">
                        <li><NavLink className="link" to="/examples/one/acervo">Acervo</NavLink></li>
                        <li><NavLink className="link" to="/examples/one/sobre">Sobre</NavLink></li>
                        <li> <button onClick={(e) => Dropdown(e.target)} className="nav-dropdown">
                            <span className="nav-dropdown-title">Mais </span><img alt="Seta para baixo do menu de links." src="/examples/one/dropdownIcon.png"/>
                            <ul className="nav-dropdown-links closable">
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                            </ul>
                        </button></li>
                    </ul>
                </nav>
                <div className="header-userarea">
                    {
                    !sessionStorage.getItem("User")?
                    <div className="header-userarea-nosession">
                        <NavLink className="link" to="/examples/one/entrar">Entrar</NavLink>
                        <span>OU</span>
                        <NavLink className="link" to="/examples/one/registrar">Criar Conta</NavLink>
                        <button className="theme-button">
                            <img className="no-invert" onClick={(e) => ChangeTheme(e.target)} alt="Ícone de mudar o tema." src="/examples/one/themeIcon.png"/>
                        </button>
                    </div>
                    :
                    <div className="header-userarea-session">
                        <div className="top-userarea">
                            <img className="user-image" alt="Imagem do usuário" 
                            src={JSON.parse(sessionStorage.getItem("User")).image? `https://kx-library-api.herokuapp.com/images/${JSON.parse(sessionStorage.getItem("User")).image}` : "/examples/one/genericUserIcon.png"}/>
                            <p onClick={() => Dropdown(null, document.getElementById("uo1"))} className="greetings">{`Olá, ${JSON.parse(sessionStorage.getItem("User")).name.substring(0, JSON.parse(sessionStorage.getItem("User")).name.indexOf(" "))}.`}
                            <img alt="" src="/examples/one/dropdownIcon.png"/></p>
                            <button className="logout-button">
                                <img alt="Ícone do botão de sair." onClick={async() => Logout()} src="/examples/one/logoutIcon.png"/>
                            </button>
                            <button className="theme-button">
                                <img className="" onClick={(e) => ChangeTheme(e.target)} alt="Ícone de mudar o tema." src="/examples/one/themeIcon.png"/>
                            </button>
                        </div>
                        <div id="uo1" className="user-options closable">
                            <ul>
                                <li><NavLink className="link" to="/examples/one/usuario">Meu Perfil</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/usuario/livros">Livros</NavLink></li>
                                <li><button className="link" onClick={() => Logout()}>Sair</button></li>
                            </ul>
                        </div>
                    </div>
                    }
                </div>
                <button onClick={(e) => RespNav()} id="resp-nav-button" className="resp-nav-button">
                    <span className="resp-nav-button-line"></span>
                    <span className="resp-nav-button-line"></span>
                    <span className="resp-nav-button-line"></span>
                </button>
            </div>
            <svg id="header-svg" preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
                <path fill="#5000ca" d="M0,160L48,181.3C96,203,192,245,288,261.3C384,277,480,267,576,245.3C672,224,768,192,864,197.3C960,203,1056,245,1152,234.7C1248,224,1344,160,1392,128L1440,96L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z"></path>
            </svg>
            <nav id="resp-nav" className="resp-nav">
                <div className="header-userarea">
                    {
                    !sessionStorage.getItem("User")?
                    <div className="header-userarea-nosession">
                        <NavLink className="link" to="/examples/one/entrar">Entrar</NavLink>
                        <span>OU</span>
                        <NavLink className="link" to="/examples/one/registrar">Criar Conta</NavLink>
                        <button className="theme-button">
                            <img className="no-invert" onClick={(e) => ChangeTheme(e.target)} alt="Ícone de mudar o tema." src={"/examples/one/themeIcon.png"}/>
                        </button>
                    </div>
                    :
                    <div className="header-userarea-session">
                        <div className="top-userarea">
                        <img className="user-image" alt="Imagem do usuário" 
                            src={JSON.parse(sessionStorage.getItem("User")).image? `https://kx-library-api.herokuapp.com/images/${JSON.parse(sessionStorage.getItem("User")).image}` : "genericUserIcon.png"}/>
                            <p onClick={() => Dropdown(null, document.getElementById("uo2"))} className="greetings">{`Olá, ${JSON.parse(sessionStorage.getItem("User")).name.substring(0, JSON.parse(sessionStorage.getItem("User")).name.indexOf(" "))}.`}
                            <img alt="" src={"/examples/one/dropdownIcon.png"}/></p>
                            <button className="logout-button">
                                <img alt="Ícone do botão de sair." onClick={async() => Logout()} src={"/examples/one/logoutIcon.png"}/>
                            </button>
                            <button className="theme-button">
                                <img className="no-invert" onClick={(e) => ChangeTheme(e.target)} alt="Ícone de mudar o tema." src={"/examples/one/themeIcon.png"}/>
                            </button>
                        </div>
                        <div id="uo2" className="user-options closable">
                            <ul>
                                <li><NavLink className="link" to="/examples/one/usuario">Meu Perfil</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/usuario/livros">Livros</NavLink></li>
                                <li><button className="link" onClick={() => Logout()}>Sair</button></li>
                            </ul>
                        </div>
                    </div>
                    }
                </div>
                <p className="title">Navegue</p>
                <ul className="header-nav-list">
                    <li><NavLink className="link" to="/examples/one/acervo">Acervo</NavLink></li>
                    <li><NavLink className="link" to="/examples/one/sobre">Sobre</NavLink></li>
                    <li> <button onClick={(e) => Dropdown(e.target)} className="nav-dropdown">
                            <span className="nav-dropdown-title">Mais </span><img alt="Seta para baixo do menu de links." src={"/examples/one/dropdownIcon.png"}/>
                            <ul className="nav-dropdown-links closable">
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                                <li><NavLink className="link" to="/examples/one/#">Link</NavLink></li>
                            </ul>
                    </button></li>
                </ul>

                <button onClick={() => RespNavClose()} className="close-bottom"><span className="close-line"></span></button>
            </nav>
        </header>
    );
}

function ChangeTheme(btn)
{
    btn.style.transform = btn.style.transform.includes("180") ? "rotate(0)":"rotate(180deg)";

    if(btn.style.transform.includes("180"))
    {
        localStorage.setItem("Theme", "Dark");
        PageTheme(localStorage.getItem("Theme"));
    }
    else
    {
        localStorage.setItem("Theme", "Light");
        PageTheme(localStorage.getItem("Theme"));
    }
}
function RespNav()
{
    ComponentClose();
    const e = document.getElementById("resp-nav-button");
    const left = e.children[0];
    const mid = e.children[1];
    const right = e.children[2];

    if(!mid.style.display.includes("none"))
    {
        mid.style.display = "none";
        left.animate([{transform: "rotate(0)"}, {transform: "rotate(45deg)"}], {duration: 200});
        left.style.position = "absolute";
        right.animate([{transform: "rotate(0)"}, {transform: "rotate(-45deg)"}], {duration: 200});
        right.style.position = "absolute";
        left.style.transform = "rotate(45deg)";
        right.style.transform = "rotate(-45deg)";
        document.getElementById("resp-nav").style.display = "flex";
        document.getElementById("resp-nav").animate(
        [{display: "flex", top: "-150px", opacity: 0.25}, {display:"flex", top: "0", opacity: 1}], 
        {duration: 300});
    }
    else
    {
        left.animate([{transform: "rotate(45deg)"}, {transform: "rotate(0)"}], {duration: 200})
        .addEventListener("finish", () =>{left.style.position = "static"; left.style.transform = "rotate(0)";});
        right.animate([{transform: "rotate(-45deg)"}, {transform: "rotate(0)"}], {duration: 200})
        .addEventListener("finish", () =>{right.style.position = "static"; right.style.transform = "rotate(0)"; mid.style.display = "block";});
        document.getElementById("resp-nav").animate(
            [{top: "0", opacity: 1}, {top: "-150px", opacity: 0}], 
            {duration: 300}).addEventListener("finish", () => {document.getElementById("resp-nav").style.display = "none";});
    }
}
function RespNavClose(inst = false)
{
    ComponentClose();
    const left = document.getElementById("resp-nav-button").children[0];
    const mid = document.getElementById("resp-nav-button").children[1];
    const right = document.getElementById("resp-nav-button").children[2];

    if(!inst && mid.style.display.includes("none"))
    {
    left.animate([{transform: "rotate(45deg)"}, {transform: "rotate(0)"}], {duration: 200})
    .addEventListener("finish", () =>{left.style.position = "static"; left.style.transform = "rotate(0)";});
    right.animate([{transform: "rotate(-45deg)"}, {transform: "rotate(0)"}], {duration: 200})
    .addEventListener("finish", () =>{right.style.position = "static"; right.style.transform = "rotate(0)"; mid.style.display = "block";});
    document.getElementById("resp-nav").animate(
        [{top: "0", opacity: 1}, {top: "-150px", opacity: 0}], 
        {duration: 300}).addEventListener("finish", () => {document.getElementById("resp-nav").style.display = "none";});
    }
    else
    {
        mid.style.display = "block";
        left.style.transform = "rotate(0)";
        right.style.transform = "rotate(0)";
        left.style.position = "static";
        right.style.position = "static";
        document.getElementById("resp-nav").style.display = "none";
    }
}
function Dropdown(e, ex)
{
    let b = null
    if(e)
    {
    b = e;
    e.children.length > 1? b = e :
    b = e.parentElement
    }

    if(ex)
    {
        !ex.style.display.includes("block")?
        ex.style.display = "block":
        ex.style.display = "none"
    }
    else
    {
        if(b.children[2].style.display.includes("flex"))
        {
            b.lastChild.style.display = "none";
        }
        else
        {
            b.lastChild.style.display = "flex";
        }
    }
}
async function Logout()
{
    await axios.delete("https://kx-library-api.herokuapp.com/logout", {withCredentials: true, 
    headers: {"Access-Control-Allow-Origin": "https://kx-library-api.herokuapp.com"}})
    .then((res) => 
    {
        if(res.data.sucess)
        {
            sessionStorage.removeItem("User");
            window.location.reload();
        }
    });
}