import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { PageTheme } from "./StyleScripts";

export default function NotFound(props)
{
    document.title = "Biblioteca X | Página desconhecida";
    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
    });
    return(
        <section className="content notfound-container light-background">
            <img alt="Ilustração de página não encontrada." src={"/examples/one/illustration_notfound.png"} />
            <h3 className="light-text">P&aacute;gina n&atilde;o encontrada...</h3>
            <NavLink to="/examples/one/">Voltar &rarr;</NavLink>
        </section>
    );
}