import { NavLink } from "react-router-dom";
import "./BookCard.css";

export default function BookCard(props)
{
    return(
        <NavLink to={"/examples/one/livro?id="+props.id} className="book light-backhover light-text">
            <img className="book-img" alt="Foto de um livro." src={props.image || "/examples/one/bookLogo.png"}/>
            <p className="label">T&iacute;tulo:</p>
            <p className="book-name">{props.name || "Livro XYZ XYZ"}</p>
            <p className="label">Autor:</p>
            <p className="book-author">{props.author || "Autor Bacana"}</p>
        </NavLink>
    );
}