import { useEffect, useState } from "react";
import { PageTheme, Simplify } from "./StyleScripts";
import "./Register.css";
import axios from "axios";
export default function Register(props)
{
    document.title = "Biblioteca X | Crie sua conta";
    document.scrollingElement.scroll({top: 0});

    const [errorMsg, setErrorMsg] = useState(null);

    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
        Simplify();
        if(sessionStorage.getItem("User"))
        {
            window.location.replace("/examples/one/");
        }
    });

    return(
        <section className="content register-container light-background">
            <div className="register-mid light-background-full">
                <div className="form-holder light-background">
                    <p className="form-title">Crie uma conta</p>
                    <svg preserveAspectRatio="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
                        <path fill="#5000ca" d="M0,320L80,304C160,288,320,256,480,256C640,256,800,288,960,288C1120,288,1280,256,1360,240L1440,224L1440,0L1360,0C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0L0,0Z"></path>
                    </svg>
                    <h5 className="error-box" id="error-box">{errorMsg}</h5>
                    <form encType="multipart/form-data" className="form" action="#" onSubmit={(e) => {e.preventDefault(); Validate(setErrorMsg);}}>
                        <label className="input-holder name-holder light-text">
                            <span className="placeholder"><img alt="" className="no-invert" src={"/examples/one/idIcon.png"}/>Seu nome</span>
                            <input id="newname" onFocus={(e) => Focus(e.target)} onBlur={(e => Unfocus(e.target))} className="name light-border light-text" type="text"/>
                        </label>
                        <label className="input-holder email-holder light-text">
                            <span className="placeholder"><img alt="" className="no-invert" src={"/examples/one/emailIcon.png"}/>Seu e-mail</span>
                            <input id="newemail" onFocus={(e) => Focus(e.target)} onBlur={(e => Unfocus(e.target))} className="email light-border light-text" type="email"/>
                        </label>
                        <label className="input-holder password-holder light-text">
                            <span className="placeholder"><img alt="" className="no-invert" src={"/examples/one/passwordIcon.png"}/>Sua senha</span>
                            <input id="newpassword" onFocus={(e) => Focus(e.target)} onBlur={(e => Unfocus(e.target))} className="password light-border light-text" type="password"/>
                            <button onClick={() => PasswordVis()} type="button" className="password-vis light-text"><img alt="" className="no-invert" src={"/examples/one/visibleIcon.png"}/></button>
                        </label>
                        <label className="input-holder light-text">
                            <img alt="" id="prev-uimg" />
                            <input type="file" />
                        </label>
                        <button className="button" type="submit">Criar</button>
                    </form>
                </div>
            <img className="illustration" alt="Ilustração da tela de login." src={"/examples/one/illustration_register.png"}/>
            </div>
        </section>
    );
}
async function Validate(errcb)
{
    const name = document.getElementById("newname").value;
    const email = document.getElementById("newemail").value;
    const password = document.getElementById("newpassword").value;
    const errbox = document.getElementById("error-box");

    if(!name || !email || !password)
    {
        ShowMessage("Informe todas as credenciais.", "warn", errcb, errbox);
    }
    else if(name && email && password)
    {
        ShowMessage(null, "clear", errcb, errbox);
        await axios.post("https://kx-library-api.herokuapp.com/register", 
        {email, password, name, image: "someuserimage", location: {country: "Brasil", state: "Rio de Janeiro", city: "Rio de Janeiro"}}, 
        {withCredentials: true, headers: {'Access-Control-Allow-Origin': 'https://kx-library-api.herokuapp.com'}})
        .then((res) =>
        {
            if(res.data.sucess)
            {
                window.location.replace("/entrar");
            }
        });
    }
}
function ShowMessage(msg = "", type = "", errcb, errbox)
{
    errcb(msg);

    switch(type)
    {
        case "warn":
            {
                errbox.style.color = "orange";
                errbox.style.borderColor = "orange";
                break;
            }
        case "error":
            {
                errbox.style.color = "orange";
                errbox.style.borderColor = "orange";
                break;
            }
        case "clear":
            {
                errbox.style.color = "transparent";
                errbox.style.borderColor = "transparent";
                break; 
            }
        default:
            {
                break;
            }
    }
}
function Focus(e)
{
    const parent = e.parentElement;

    if(e.value.length < 1)
    {
        parent.firstChild.animate([{fontSize: "20px", color: "gray", paddingBottom: "0"},
        {fontSize: "18px", color:"inherit", paddingBottom: "75px"}],
        {duration: 200});
        parent.firstChild.style.fontSize = "18px";
        parent.firstChild.style.paddingBottom = "75px";
        parent.firstChild.style.color = "inherit";
    }
}
function Unfocus(e)
{
    const parent = e.parentElement;

    if(e.value.length === 0)
    {
        parent.firstChild.animate([{fontSize: "18px"}, 
        {fontSize: "20px"}],
        {duration: 200});
        parent.firstChild.style.fontSize = "20px";
        parent.firstChild.style.paddingBottom = "0";
        parent.firstChild.style.color = "gray";
    }
}
function PasswordVis(icon)
{
    const input = document.getElementById("newpassword");

    input.type === "password"? input.type = "text" : input.type = "password";
    input.focus();
}