import axios from "axios";

export async function LoadBooks()
{
    await axios.get("https://kx-library-api.herokuapp.com/book", {withCredentials: true, 
    headers: {"Access-Control-Allow-Origin": "https://kx-library-api.herokuapp.com"}})
    .then((res) => {return sessionStorage.setItem("Books", JSON.stringify(res.data))});
}
export async function FetchData()
{
    await axios.get("https://kx-library-api.herokuapp.com/user-data", {withCredentials: true, 
    headers: {"Access-Control-Allow-Origin": "https://kx-library-api.herokuapp.com"}})
    .then((res) => 
    {
        if(res.data.sucess)
        {
            sessionStorage.setItem("User", JSON.stringify(res.data.user));
        }
        else
        {
            sessionStorage.removeItem("User");
        }
    });
}