import { useEffect } from "react"
import { PageTheme } from "./StyleScripts";
import "./Profile.css";

export default function Profile(props)
{
    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
        if(!sessionStorage.getItem("User"))
        {
            window.location.replace("/examples/one/login/");
        }
    });

    return(
        <section className="content profile-container light-background">
            <div className="profile-mid light-background-full">
                <div className="user-info">
                    <img className={JSON.parse(sessionStorage.getItem("User")).image? "user-image" : "user-image guser-image"} alt="Sua imagem de usuário." src={JSON.parse(sessionStorage.getItem("User")).image?
                    "https://kx-library-api.herokuapp.com/images/"+JSON.parse(sessionStorage.getItem("User")).image
                    : "/examples/one/genericUserIcon.png"}/>
                    <div className="user-namerole">
                    <h3 className="user-name light-text">{JSON.parse(sessionStorage.getItem("User")).name}</h3>
                    <h5 className="user-role">{JSON.parse(sessionStorage.getItem("User")).role || "Usuário da Biblioteca X"}</h5>
                    </div>
                </div>
            </div>
        </section>
    )
}