export default function BackTopButton(props)
{
    return(
        <button onClick={(e) => ScrollTop(e)} title="Voltar ao topo." className="backtop-btn">
            <img alt="Ícone de seta para o topo." className="no-invert" src={__dirname+"arrowIcon.png"}></img>
        </button>
    )
}

function ScrollTop(e)
{
    document.scrollingElement.scroll({top: 0});
}