import { useEffect, useState } from "react";
import BookCard from "./BookCard";
import RecBookCard from "./RecBookCard";
import "./Bookshelf.css";
import { PageTheme, Simplify } from "./StyleScripts";

export default function Bookshelf(props)
{
    const [qBooks, setQBooks] = useState(null);
    const [errorMsg, setErrorMsg] = useState(null);
    const [subpage, setSubpage] = useState(1);
    
    document.title = "Biblioteca X | Acervo";
    
    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
        Simplify();
    }, [subpage, qBooks]);

    return(
        <section className="content bookshelf-container light-background">
            <aside className="bookshelf-nav light-background-full light-text">
                <h4 className="title light-text">Acervo</h4>
                <ul>
                <li onClick={(e) => ChangeSubpage(e.target, subpage, setSubpage)} 
                        subpage={1}><button className="light-backhover"
                        subpage={1}>
                        <img
                        subpage={1} className="no-invert" alt="Imagem botão Buscar." src={"/examples/one/mglassIcon.png"}/>
                        <p 
                        subpage={1} className="light-text">Buscar</p>
                        </button></li>
                    <li onClick={(e) => ChangeSubpage(e.target, subpage, setSubpage)} 
                        subpage={2}><button className="light-backhover"
                        subpage={2}>
                        <img
                        subpage={2} className="no-invert" alt="Imagem botão Recomendados." src={"/examples/one/fingerIcon.png"}/>
                        <p
                        subpage={2} className="light-text">Recomendados</p>
                        </button></li>
                    <li onClick={(e) => ChangeSubpage(e.target, subpage, setSubpage)} 
                        subpage={3}><button className="light-backhover"
                        subpage={3}>
                        <img
                        subpage={3} className="no-invert" alt="Imagem botão Sugestões." src={"/examples/one/suggestionIcon.png"}/>
                        <p
                        subpage={3} className="light-text">Sugest&otilde;es</p>
                        </button></li>
                </ul>
            </aside>
            <div className="bookshelf-mid light-background-full light-text">
                {
                    RenderSubpage(subpage, setQBooks, setErrorMsg, qBooks, errorMsg)
                }
            </div>
        </section>
    );
}

function Focus(i)
{
    if(document.getElementById("nocontent-dots").children[0].getAnimations().length<1)
    {
        for(i=0; i<document.getElementById("nocontent-dots").children.length; i++)
        {
            document.getElementById("nocontent-dots").children[i].animate(
            [{width: "20px", height: "20px"}, {width: "25px", height: "25px"}], 
            {duration: 450, iterations: Infinity, direction: "alternate", delay: i*250});
        }
    }
}
function Unfocus(i)
{
    for(i=0; i<document.getElementById("nocontent-dots").children.length; i++)
    {
        document.getElementById("nocontent-dots").children[i].getAnimations().forEach((a) => a.cancel());
    }
}

function Search(i, setBooks, setError)
{
    if(i.value.length > 0)
    {
        const books = JSON.parse(sessionStorage.getItem("Books"));
        if(books)
        {
            document.getElementById("nocontent-fill").style.display = "none";
            const matched = books.filter((b) => 
            b.name.toLowerCase().trim().includes(i.value.toLowerCase().trim()) 
            ||
            b.author.toLowerCase().trim().includes(i.value.toLowerCase().trim()));

            if(matched.length > 0)
            {
                setBooks(matched);
                setError(null);
            }
            else
            {
                setBooks(null);
                setError("Nenhum livro encontrado.");
            }
        }
        else
        {
            setBooks(null);
            setError(null);
        }
    }
    else
    {
        setBooks(null);
        setError(null);
        document.getElementById("nocontent-fill").style.display = "flex";
    }
}
function ChangeSubpage(e, subpage, setSubpage)
{
    const next = parseInt(e.getAttribute("subpage"));
    if(next && subpage !== next)
    {
        setSubpage(next);
    }
}
function RenderSubpage(subpage, setQBooks, setErrorMsg, qBooks, errorMsg)
{
    switch(subpage)
    {
    case 1:
    {
        return(
    <div className="book-search-container">
        <h3 className="title">Buscar</h3>
        <p className="text">Olá, como vai? Já tem um livro em mente? Pode procurar pelo nome, autor ou coleção.</p>
        <input id="search-book" onFocus={(e) => Focus(e.target)} onBlur={(e) => Unfocus(e.target)}
        onChange={(e) => Search(e.target, setQBooks, setErrorMsg)}
        className="book-search light-boxshadow" 
        placeholder="Livro, autor, coleção..."/>
        <div className="results">
            <div id="nocontent-fill" className="nocontent-fill">
                <div id="nocontent-dots" className="nocontent-dots">
                    <span className="nocontent-dot"></span>
                    <span className="nocontent-dot"></span>
                    <span className="nocontent-dot"></span>
                </div>
                <p className="nocontent-txt">Pesquise algo...</p>
            </div>
            {
                qBooks?
                qBooks.map((b, i) =>
                <BookCard key={i} name={b.name} author={b.author} id={b._id} />)
                :
                errorMsg?
                <p className="title">{errorMsg}</p>
                :
                null
            }
        </div>
    </div>)
    }
    case 2:
    {return(
    <div className="featured-container">
        <h3 className="title">Recomendados</h3>
        <p className="text">Aberto(a) à sugestões? Então dê uma olhada nos Recomendados.</p>
        <div className="featured-card-container">
            <RecBookCard />
            <RecBookCard />
            <RecBookCard />
            <RecBookCard />
            <RecBookCard />
        </div>
    </div>)
    }
    case 3:
    {
        return(
            <div className="suggestion-container">
                <h3 className="title">Sugest&otilde;es</h3>
                <p className="text">Gostaria de ler um livro que n&atilde;o est&aacute; aqui? Envie sua sugest&atilde;o para sabermos.</p>
            </div>
        );
    }
    default:
        return null
    }
}