import { NavLink } from "react-router-dom";

export default function Breadcrumb(props)
{
    return (
        <div className="breadcrumb light-background-full">
            {Pathmake(props.path)}
        </div>
    );
}

function Pathmake(path)
{
    let paths = path.split("/");

    return (
        <nav className="light-text">
            <span><NavLink className="link light-text" to="/examples/one/"><img className="no-invert" alt="" src={"/examples/one/homeIcon.png"}/>Biblioteca X</NavLink> &gt; </span>
            {
                paths.map((p, i) =>
                {
                    if(p.length > 0)
                    {
                        const plink = path.substring(0, (path.indexOf("/"+p)+p.length+1));
                        return (
                        <span key={i}> <NavLink className="link light-text" to={plink}>{p.replace(p.charAt(0), p.charAt(0).toUpperCase())}</NavLink> &gt; </span>
                        );
                    }
                    else
                    {
                        return null;
                    }
                })
            }
        </nav>
    )
}