import { NavLink } from "react-router-dom";
import "./BookCard.css";

export default function RecBookCard(props)
{
    return(
        <NavLink to={"/examples/one/livro?id="+props.id} className="book light-backhover light-text">
            <img className="book-img" alt="Foto de um livro." src={props.image || "/examples/one/bookLogo.png"}/>
            <p className="label">Título:</p>
            <p className="book-name">{props.name || "Livro Interessante"}</p>
            <p className="label">Recomendado por:</p>
            <p className="book-recby">{props.recby || "Prof. Juarez Inácio Moisés"}</p>
        </NavLink>
    );
}