import { useEffect } from "react";
import { NavLink } from "react-router-dom";
import "./Home.css";
import { PageTheme, Simplify } from "./StyleScripts";

export default function Home(props)
{
    document.title = "Biblioteca X";
    document.scrollingElement.scroll({top: 0});
    
    useEffect(() =>
    {
        PageTheme(localStorage.getItem("Theme") || "Light");
        Simplify();
    });

    return(
        <section className="content home-container light-background">
            <div className="home-left light-background-full light-text">
                
            </div>
            <div className="home-mid light-background-full light-text">
                <h3 className="title">Ol&aacute;. Seja bem-vindo(a)!</h3>
                <img className="home-illustration" alt="Ilustração da página inicial." src={"/examples/one/illustration_home.png"}/>
                <p className="text">Já sabe qual livro quer ler hoje? Por aqui, na nossa biblioteca virtual, você pode escolher um livro
                por conta ou ver as recomendações dos professores! Basta criar sua conta, ir para a página <NavLink className="link" to="/examples/one/acervo">Acervo</NavLink>, procurar o livro desejado ou ir na aba "Recomendados", escolher 
                e buscar! Pronto, agora você tem até um mês para ler, e se quiser ficar por mais tempo pode pedir por aqui também. 
                Esperamos que goste da experiência, e boa leitura!</p>
            </div>
            <div className="home-right light-background-full light-text">
                <h4 className="title">Links R&aacute;pidos</h4>
                <ul className="link-list">
                    <li><NavLink className="link light-text" to="/examples/one/acervo">Acervo</NavLink></li>
                    <li><NavLink className="link light-text" to="/examples/one/sobre">Sobre</NavLink></li>
                    <li><NavLink className="link light-text" to="/examples/one/">link</NavLink></li>
                    <li><NavLink className="link light-text" to="/examples/one/">link</NavLink></li>
                    <li><NavLink className="link light-text" to="/examples/one/">link</NavLink></li>
                </ul>
            </div>
        </section>
    );
}