import { Route, Switch, withRouter } from "react-router";
import Book from "./Book";
import Bookshelf from "./Bookshelf";
import Home from "./Home";
import Login from "./Login";
import NotFound from "./NotFound";
import Profile from "./Profile";
import Register from "./Register";

export default function Content(props)
{
    return(
        <Switch>
            <Route exact path="/examples/one/" component={withRouter(Home)}></Route>
            <Route exact path="/examples/one/acervo" component={withRouter(Bookshelf)}></Route>
            <Route exact path="/examples/one/livro" component={withRouter(Book)}></Route>
            <Route exact path="/examples/one/entrar" component={withRouter(Login)}></Route>
            <Route exact path="/examples/one/registrar" component={withRouter(Register)}></Route>
            <Route exact path="/examples/one/usuario" component={withRouter(Profile)}></Route>

            <Route component={withRouter(NotFound)}></Route>
        </Switch>
    );
}